import React from 'react'

class MemeGenerator extends React.Component{
   constructor(){
       super()
       this.state={
        topText:"",
        bottomText:"",
        randomImage:"https://i.imgflip.com/1bij.jpg",
        allMemeImgs:[]
       }
       this.handleChange=this.handleChange.bind(this)
       this.handleSubmit=this.handleSubmit.bind(this)
   }

   componentDidMount(){
       fetch("https://api.imgflip.com/get_memes")
        .then(response => response.json())
        .then(response=>{
            const {memes}= response.data //creating memes array from response.data
            console.log(memes[0]) //jsut for testing
            this.setState({
                allMemeImgs:memes
            })
        })
   }

   handleChange(event){
       this.setState({
           [event.target.name]:event.target.value
       })
       /*alt const{name, value}= event.target
       this.setState({
           [name]:value
        })*/
   }

   handleSubmit(event){
       event.preventDefault()
        const randomNum= Math.floor(Math.random()*this.state.allMemeImgs.length) //*this.state.allMemeImgs.length to be able to get any img inside the array
        const randomMemeImag=this.state.allMemeImgs[randomNum].url
        //updating state with this img
        this.setState({
            randomImage: randomMemeImag
        })

   }

   render(){
       return(
           <div>
               <form className="meme-form" onSubmit={this.handleSubmit}> 
               <input name="topText" value={this.state.topText} type="text" onChange={this.handleChange} placeholder="Top Text"/><br/>
               <input name="bottomText" value={this.state.bottomText} type="text" onChange={this.handleChange} placeholder="Bottom Text"/><br/>
               <button >Generate</button>
               </form>
               <div className="meme">
                   <img src={this.state.randomImage} alt="problema"/>
                    <h2 className="top">{this.state.topText}</h2>
                    <h2 className="bottom">{this.state.bottomText}</h2>
               </div>
        </div>
       )
   }

}



export default MemeGenerator